import React from 'react'
import './Navbar.scss'
// import store from '../../store';

class Navbar extends React.Component {
    constructor(props) {
        super(props)
        console.log(props);
        this.state = {
            city: ''
        }
    }

    inputValue = (e) => {
        e.preventDefault();
        this.setState({
            city: e.target.value
        })
    }
    clickHandle = (e) => {
        e.preventDefault();
        this.props.getApi(this.state.city);
        console.log(this.state);
    }
    render() {
        return <div className="navbar">
            <form>
                <input
                    className="input"
                    type="text"
                    name="city"
                    placeholder="sydney"
                    onChange={this.inputValue}
                // value={this.state.value} //打开试一下，在文档中看到改为受控组件
                />
                <button onClick={(e) => { this.clickHandle(e) }}>search</button>
            </form>
        </div>
    }
}

export default Navbar

