import axios from 'axios'
import React from 'react'
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
// import { faCloud } from '@fortawesome/free-solid-svg-icons'
import './Main.scss'
import '../../img/avatar1.jpeg'

const city = 'sydney'
const API_key = "f8d071630563e89c05f97be774f43a8b"
const api = `http://api.openweathermap.org/data/2.5/forecast?q=${city}&appid=${API_key}`

class DayInfo extends React.Component {
    constructor(props) {
        super(props)
        console.log(props);
        this.state = {
            day: '',
            icon: '',
            highTemp: '',
            lowTemp: ''
        }
    }
    async componentDidMount() {
        try {
            const res = await axios.get(api);
            // console.log(res);
            // const currentTime = parseInt(new Date().getHours() / 3);
            // const day = new Date().getDay();
            // console.log(day);
            const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
            const index = new Date().getDay() + this.props.index < 7 ? new Date().getDay() + this.props.index : new Date().getDay() + this.props.index - 7;
            const icon = res.data.list[this.props.index + this.props.index * 7].weather[0].icon
            const highTemp = res.data.list[this.props.index + this.props.index * 7].main.temp_max
            const lowTemp = res.data.list[this.props.index + this.props.index * 7].main.temp_min
            this.setState({
                day: days[index],
                icon: icon,
                highTemp: parseInt(highTemp - 273.15),
                lowTemp: parseInt(lowTemp - 273.15)
            })
        } catch (e) {
            console.log(e.message);
        }
    }

    render() {
        // const element = <FontAwesomeIcon icon={faCloud} />
        // const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
        // const index = new Date().getDay() + this.props.index < 7 ? new Date().getDay() + this.props.index : new Date().getDay() + this.props.index - 7;
        return <div className="dayInfo">
            <h3 className="dayInfo-day">{this.state.day}</h3>
            <div className="dayInfo-icon" >
                <img src={`http://openweathermap.org/img/w/${this.state.icon}.png`} alt="img"></img>
            </div>
            <div className="dayInfo-highTemp">{this.state.highTemp}</div>
            <div className="dayInfo-lowTemp">{this.state.lowTemp}</div>
        </div>
    }
}

export default DayInfo