import React from 'react'
import axios from 'axios'
import './Weather.scss'
import Header from '../Header/Header'
import Main from '../Main/Main'
import Navbar from '../Navbar/Navbar'



class Weather extends React.Component {
    constructor() {
        super()
        console.log(this);
        this.state = {
            res: '',
            city: 'sydney',
            temp: '',
            weather: '',
            humidity: '',
            wind: ''
        }
    }
    componentDidMount() {
        this.getDefault()
        console.log(this.getDefault());
    }
    getDefault = async () => {
        const city = 'sydney'
        const API_key = "f8d071630563e89c05f97be774f43a8b"
        const api = `http://api.openweathermap.org/data/2.5/forecast?q=${city}&appid=${API_key}`
        const res = await axios.get(api)
        this.setState({
            temp: parseInt(res.data.list[0].main.temp - 273.15),
            weather: res.data.list[0].weather[0].description,
            humidity: res.data.list[0].main.humidity + '%',
            wind: res.data.list[0].wind.speed + 'm/s'
        })
    }
    getApi = async (city) => {
        const API_key = "f8d071630563e89c05f97be774f43a8b";
        const api = `http://api.openweathermap.org/data/2.5/forecast?q=${city}&appid=${API_key}`;
        const res = await axios.get(api);
        console.log(res);
        this.setState({
            res: res,
            city: res.data.city.name,
            temp: parseInt(res.data.list[0].main.temp - 273.15),
            weather: res.data.list[0].weather[0].description,
            humidity: res.data.list[0].main.humidity + '%',
            wind: res.data.list[0].wind.speed + 'm/s'
        })
        console.log(this.state.res);
    }
    render() {
        console.log(this.state.city);
        return (
            <div className="weather" >
                <Navbar getApi={this.getApi} />
                <div className="weather-card">
                    <Header res={this.state.res}
                        city={this.state.city}
                        temp={this.state.temp}
                        weather={this.state.weather}
                        humidity={this.state.humidity}
                        wind={this.state.wind}
                    />
                    <Main res={this.state.res} />
                </div>
            </div>
        )
    }
}

export default Weather